import React from 'react';
import { ApplyPluginsType } from '/home/culler/code/workspace/larp-front/node_modules/umi/node_modules/@umijs/runtime';
import * as umiExports from './umiExports';
import { plugin } from './plugin';

export function getRoutes() {
  const routes = [
  {
    "path": "/login",
    "component": require('@/pages/Login').default,
    "exact": true
  },
  {
    "path": "/",
    "component": require('@/pages/index').default,
    "redirect": "/login",
    "exact": true
  },
  {
    "path": "/",
    "component": require('@/layouts').default,
    "routes": [
      {
        "path": "/gamer",
        "component": require('@/pages/Gamer').default,
        "exact": true
      },
      {
        "path": "/admin",
        "component": require('@/pages/Admin').default,
        "exact": true
      },
      {
        "path": "/dm",
        "component": require('@/pages/DM').default,
        "exact": true
      }
    ]
  }
];

  // allow user to extend routes
  plugin.applyPlugins({
    key: 'patchRoutes',
    type: ApplyPluginsType.event,
    args: { routes },
  });

  return routes;
}
